@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Muli:500&display=swap);
/* Headings */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Lato", sans-serif;
  font-weight: 600;
  margin: 0 0 1.2em; }
  h1:only-child,
  h2:only-child,
  h3:only-child,
  h4:only-child,
  h5:only-child,
  h6:only-child {
    margin: 0; }
  h1:last-child,
  h2:last-child,
  h3:last-child,
  h4:last-child,
  h5:last-child,
  h6:last-child {
    margin: 0; }

h2 {
  margin: 0 0 20px; }

p {
  margin: 0 0 12px; }

*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-overflow-scrolling: touch; }

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: "Lato", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  text-decoration: none;
  color: inherit; }

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

.page-logo {
  position: absolute;
  top: 5px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 100px;
  -webkit-filter: drop-shadow(4px 10px 4px #000000);
          filter: drop-shadow(4px 10px 4px #000000); }
  .page-logo a,
  .page-logo img {
    display: block; }
  .page-logo img {
    width: 100%;
    height: auto; }

.page-hamburger button {
  outline: none;
  border: none;
  background: transparent; }

.page-hamburger img {
  width: 20px;
  height: auto;
  display: block; }

.btn {
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
  padding: 12px 32px;
  font-family: "Ubuntu", sans-serif;
  font-weight: 500;
  font-size: 24px;
  color: #fff;
  background: #000000;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  border-radius: 6px;
  cursor: pointer;
  border: none;
  outline: none; }
  .btn.disabled {
    opacity: 0.56;
    cursor: no-drop; }
    .btn.disabled:hover {
      background: #000000; }
  .btn:hover {
    background: #2e0b29; }
  .btn-chords-select {
    padding: 0;
    width: 24px;
    height: 24px;
    display: block;
    border-radius: 50%;
    background-color: white; }
    .btn-chords-select.selected {
      background-color: rgba(0, 0, 0, 0.15);
      background-image: url(/mychords/static/media/checked-black.5aa49054.svg); }

.Header {
  position: relative;
  z-index: 3;
  padding: 30px 30px;
  background-color: #000000;
  display: flex;
  align-items: center;
  justify-content: flex-end; }

.Footer {
  padding: 30px 30px 10px;
  margin-top: auto;
  color: rgba(255, 255, 255, 0.8);
  background-color: #000000;
  text-align: center; }
  .Footer__copyrights {
    font-size: 14px; }
  .Footer__link {
    -webkit-transition: all 0.15s ease-in-out;
    transition: all 0.15s ease-in-out; }
    .Footer__link:hover {
      color: #f0e10c; }

.GameForm {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  position: relative;
  text-align: left;
  font-size: 22px; }
  .GameForm__title {
    flex: 0 0 100%;
    max-width: 100%;
    text-align: center; }
  .GameForm__item {
    flex: 0 0 auto;
    margin: 0 25px 25px 0;
    display: flex;
    align-items: center; }
  .GameForm__btn-wrapper {
    flex: 0 0 100%;
    min-width: 100%;
    text-align: center; }
  .GameForm__input {
    display: block; }
  .GameForm__label {
    display: block;
    margin: 0;
    padding-left: 10px; }

.GameArea {
  text-align: center; }

.whiteKey,
.blackKey {
  cursor: pointer;
  border: none;
  outline: none; }

.whiteKey {
  z-index: 1;
  position: relative;
  display: block;
  width: 36px;
  height: 90px;
  border: none;
  background: #fff;
  border: 1px solid #000000; }
  .whiteKey.active, .whiteKey:active {
    background: rgba(75, 26, 79, 0.3); }

.blackKey {
  z-index: 2;
  position: absolute;
  display: block;
  width: 20px;
  height: 50px;
  background: black;
  border: none;
  top: 0; }
  .blackKey.active, .blackKey:active {
    background: #4b1a4f; }
  .blackKey:nth-child(2) {
    left: 26px; }
  .blackKey:nth-child(4) {
    left: 62px; }
  .blackKey:nth-child(7) {
    left: 134px; }
  .blackKey:nth-child(9) {
    left: 170px; }
  .blackKey:nth-child(11) {
    left: 204px; }

.Game__keyboard {
  max-width: 100%;
  padding: 0 15px;
  margin: 0 auto;
  overflow-x: auto;
  display: flex;
  justify-content: center; }
  @media all and (max-width: 639px) {
    .Game__keyboard {
      display: block; } }

.Game__answer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 36px;
  letter-spacing: 1px;
  font-weight: 500;
  height: 54px;
  color: #4b1a4f;
  font-weight: 500; }
  @media all and (min-width: 768px) {
    .Game__answer {
      height: 74px; } }

.Game__title-summary {
  font-size: 40px;
  font-weight: 500; }

.Game__error-message {
  color: #b00922;
  font-weight: 400;
  font-size: 18px; }
  @media all and (min-width: 768px) {
    .Game__error-message {
      font-size: 24px; } }

.keyboard {
  display: flex;
  position: relative;
  flex: 0 0 auto; }
  @media all and (max-width: 639px) {
    .keyboard {
      width: 252px;
      margin: 0 auto;
      margin-bottom: 12px; } }

.chord {
  padding: 5px 10px;
  font-size: 22px;
  letter-spacing: 1px;
  font-weight: 500; }
  @media all and (min-width: 768px) {
    .chord {
      padding: 10px;
      font-size: 36px; } }

.btn-answer {
  margin-bottom: 15px; }

.ContentModule {
  flex: 1 1 100%;
  position: relative;
  padding-top: 85px;
  background-image: url(/mychords/static/media/piano.b75b4feb.svg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain; }
  @media all and (min-width: 768px) {
    .ContentModule {
      padding-top: 120px; } }
  @media all and (min-width: 992px) {
    .ContentModule {
      background-image: url(/mychords/static/media/piano.711ec229.jpg);
      background-position: top right;
      background-size: cover;
      background-repeat: no-repeat; } }
  .ContentModule:after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.75); }
  .ContentModule__screen {
    position: relative;
    z-index: 2;
    margin: 0 auto 10px;
    padding: 30px 20px 30px;
    text-align: center;
    min-height: 300px;
    background: rgba(250, 250, 250, 0.75);
    box-shadow: 4px 7px 22px -2px rgba(0, 0, 0, 0.15); }
    @media all and (min-width: 768px) {
      .ContentModule__screen {
        min-width: 60%;
        max-width: 90%;
        border-radius: 8px; } }
    @media all and (min-width: 1200px) {
      .ContentModule__screen {
        min-width: 66%;
        max-width: 66%; } }

